import YxTab from '@/components/common/yxTab.vue'
import {
	AppModel
} from '@/models/app.js'
const appModel = new AppModel()

export default {
	components: {
		YxTab
	},
	data() {
		return {
			noDataUrl: '',
			dataArray: [],
			pagination: {
				accessToken: 1,
				dataString: {
					limit: 10,
					offset: 0,
					where: {
						AND: [{
								created_at_gte: ""
							},
							{
								created_at_lte: ""
							}
						],
						OR: [{
							// number_eq: ""
						}]
					}
				}
			},
			loadMoreText: "加载中...",
			showLoadMore: false,
			isRefresh: true,
			selectedTab: 0,
			tabData: {
				isActive: 0,
				tabName: ["获取记录", "送出记录"]
			}
		}
	},
	filters: {
		formatStarName(val) {
			switch (val) {
				case 2:
					return '任务'
				case 3:
					return '抽奖'
				case 7:
					return '赠送'
				case 9:
					return '奖励'
				case 10:
					return '补偿'
			}
		}
	},
	onLoad: function(options) {
		this.getInitData(this.pagination)
	},
	onUnload() {
		this.dataArray = []
		this.loadMoreText = "加载更多"
		this.showLoadMore = false
	},
	//页面上拉触底事件的处理函数
	onReachBottom() {
		if (!this.isRefresh) {
			this.loadMoreText = "~ 没有更多数据了 ~"
			return
		}
		this.loadMoreText = "加载中..."
		this.showLoadMore = true
		this.pagination.dataString.page++
		this.getInitData(this.pagination)
	},
	//页面下拉初始化刷新
	onPullDownRefresh() {
		this.dataArray = []
		this.pagination.dataString.page = 0
		this.loadMoreText = ""
		this.getInitData(this.pagination)
	},
	methods: {
		getInitData(page) {
			uni.showLoading({
				title: '正在加载...'
			})
			appModel.getList(this.pagination)
				.then(res => {
					console.log(res)
					this.dataArray = res.data.arr
					this.isRefresh = false
					// if (res.data.list.length < this.pagination.size) {} else {
					// 	this.isRefresh = true
					// }
					uni.hideLoading()
					uni.stopPullDownRefresh()
				})
				.catch(err => {
					console.log(err)
					uni.hideLoading()
					uni.stopPullDownRefresh()
				})

		},
		changeTab(index) {
			this.showLoadMore = false
			this.dataArray = []
			this.selectedTab = index;
			this.getInitData(0);
			console.log(this.selectedTab)
		}
	}
}
